
/*   api [https://1forge.com/forex-data-api/api-documentation] */
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
components: {
XInput,
Group,
PopupRadio,
Cell
},
data() {
return {
apiKey: "fcfc3e8ec3786e71dd638de39e301d0b",
cal1: "CNY",
cal2: 'PHP',
num1: 0,
nowCurr: 0,
time: '',
list1: []
};
},
computed : {
num2: function () {
return this.num1 * this.nowCurr;
}
},
watch: {
cal1: function (item) {
this.getCurrency()
},
cal2: function (item) {
this.getCurrency()
}
},

methods: {
getAllCountry() {
var _self = this;
this.$http.get("/getList").then(res => {
if (res.data.error_code === 0) {
_self.list1 = _self.changeData(res.data.result.list);
} else {
console.error("");
}
});
},

changeData(data) {
var oData = data && data.length > 0 ? data : [];
var nData = [];
oData.forEach(item => {
nData.push(item.name + item.code);
});
return nData;
},
getCurrency() {
var _self = this;
this.$http.get(`/currency?key=fcfc3e8ec3786e71dd638de39e301d0b&from=${_self.cal1.match(/[a-zA-Z]+/)}&to=${_self.cal2.match(/[a-zA-Z]+/)}`)
.then((items) => {
var item = items.data;
if (item.error_code == 0) {
_self.time = item.result[0].updateTime
_self.nowCurr = item.result[0].result
} else {
console.log('')
}
})
}
},
mounted: function() {
this.getAllCountry();
this.getCurrency();
}
};



